<template>
  <div>
    <input type="text" v-model="bname" placeholder="请输入桥梁信息关键字"/>
    <button @click="getBridge(bname)">确定</button>
    <el-table :data="tableData2" @row-click="test" style="width: 100%">
      <el-table-column prop="name" label="书籍名称" width="180">
      </el-table-column>
      <el-table-column prop="author" label="作者" width="180">
      </el-table-column>
      <el-table-column prop="publisher" label="出版社"> </el-table-column>
      <el-table-column prop="price" label="价格"> </el-table-column>
      <el-table-column fixed="right" label="操作" width="300">
        <template slot-scope="scope">
            <el-button type="text" size="small" circle="true" @click="delete1(scope.row.name,scope.row.author,scope.row.publisher,scope.row.price)">删除</el-button> 
            <el-button type="text" size="small" circle="true" @click="update(scope.row.name,scope.row.author,scope.row.publisher,scope.row.price)">修改</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "findBook",
  methods: {
    handleClick(row) {
      console.log(row);
    },
    getBridge(bname) {
      for(var i=0;i<this.tableData.length;i++)
        {
            if(this.tableData[i].name.includes(bname)||this.tableData[i].author.includes(bname)||this.tableData[i].publisher.includes(bname))
            {
                this.tableData2.push(this.tableData[i])
            }
        }
        if(this.tableData2.length==0)
        {
            this.$message({
                    type: 'error',
                    message: '查无此书！'
                    })
        }
    },
    delete1(bname,bauthor,bpublisher,bprice) {
      this.$confirm('此操作将删除该条数据,是否继续？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          // 确定
                var prelen=window.tableData.length;
                console.log(prelen);
                for(var i=0;i<window.tableData.length;i++)
                {
                    if(window.tableData[i].name==bname&&window.tableData[i].author==bauthor
                    &&window.tableData[i].publisher==bpublisher&&window.tableData[i].price==bprice)
                    {
                        console.log('来了');
                        window.tableData.splice(i,1);
                    }
                }
                var nowlen=window.tableData.length;
                console.log(nowlen);
                if(nowlen<prelen)
                {
                    this.$message({
                    type: 'success',
                    message: '删除成功!'
                    }),
                    this.tableData=window.tableData;
                    this.tableData2=[];
                    for(var j=0;j<this.tableData.length;j++)
                     {
                        if(this.tableData[j].name.includes(this.bname)||this.tableData[j].author.includes(this.bname)||this.tableData[j].publisher.includes(this.bname))
                        {
                            this.tableData2.push(this.tableData[j])
                        }
                      }
                    this.bname=''
                }
                else{
                   this.$message({
                    type: 'error',
                    message: '删除失败!'
                    })
                }
              },)
        .catch(() => {
          // 取消
        })
    },
    update(bname,bauthor,bpublisher,bprice){
        this.$router.push({ name: "updateBook", query: { name: bname ,author:bauthor
        ,publisher:bpublisher,price:bprice} });
    }
  },
  data() {
    return {
      tableData: [],
      tableData2: [],
      bname:''
    };
  },
  mounted() {
    this.tableData=window.tableData
  }
};
</script>